<template>
  <div>
    <el-table
        :data="data.filter(data => !search || data.goodsName.toLowerCase().includes(search.toLowerCase()))"
        stripe
        max-height="450"
        :default-sort = "{prop: 'date', order: 'descending'}"
        border
        style="width: 100%">
      <el-table-column
          width="110"
          fixed
          sortable
          label="申请日期"
          prop="created">
      </el-table-column>
      <el-table-column
          fixed
          label="商品名称"
          width="110"
          prop="goodsName">
      </el-table-column>
      <el-table-column
          width="110"
          label="所属店铺"
          prop="shopName">
      </el-table-column>
      <el-table-column
          width="80"
          label="价格(元)"
          prop="price">
      </el-table-column>
      <el-table-column
          width="80"
          label="折扣(折)"
          prop="discount">
      </el-table-column>
      <el-table-column
          width="80"
          label="图片">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <img :src="scope.row.picGoods" class="pic">
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">查看</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
          width="270"
          label="简介"
          prop="description">
      </el-table-column>
      <el-table-column
          fixed="right"
          label="操作">
        <template slot-scope="scope">
          <el-button
              v-if="!isAll"
              size="mini"
              type="danger"
              @click="changeRequest(scope.row)">通过</el-button>
          <el-button
              v-if="isAll"
              size="mini"
              type="danger"
              @click="changeRequest(scope.row)">禁用</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "productTable",
  props: ['data', 'search', 'isAll'],
  data() {
    return {
    }
  },
  methods: {
    changeRequest(val){
      this.$bus.$emit('changeProductPass', val)
    },
  },
}
</script>

<style scoped>
.pic{
  width: 200px;
  height: 200px;
}
</style>